<template>
  <v-layout
    v-touch="{
      left: () => swipe('Left'),
      right: () => swipe('Right'),
      up: () => swipe('Up'),
      down: () => swipe('Down')
    }"
  >
    <v-subheader>Swipe Direction test004</v-subheader>
    {{ swipeDirection }}
  </v-layout>
</template>

<script>
  export default {
    data: () => ({
      swipeDirection: 'None'
    }),

    methods: {
      swipe (direction) {
        this.swipeDirection = direction
      }
    }
  }
</script>